color: $secondary-font;\r\n      font-weight: 600;\r\n\r\n      &:hover {\r\n        color: $gray2;\r\n      }\r\n\r\n      &.is-selected {\r\n        color: $black;\r\n      }\r\n\r\n      &:last-child {\r\n        @include flexbox($items: center);\r\n      }\r\n    }\r\n  }\r\n\r\n  &__service {\r\n    @include flexbox($items: center);\r\n\r\n    &__list {\r\n      color: $secondary-font;\r\n    }\r\n\r\n    &__link {\r\n      position: relative;\r\n      display: block;\r\n      padding: rem(11px) rem(12px);\r\n      color: $secondary-font;\r\n\r\n      &.dot {\r\n        &::after {\r\n          @include centerTranslateY;\r\n\r\n          content: '';\r\n          position: absolute;\r\n          right: 0;\r\n          width: 3px;\r\n          height: 3px;\r\n          border-radius: 3px;\r\n          background: $gray7;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","@use 'sass:math';\r\n\r\n// padding 관련 믹스인\r\n@mixin paddingX($paddingX: 0) {\r\n  padding-left: rem($paddingX);\r\n  padding-right: rem($paddingX);\r\n}\r\n\r\n@mixin paddingY($paddingY: 0) {\r\n  padding-top: rem($paddingY);\r\n  padding-bottom: rem($paddingY);\r\n}\r\n\r\n// margin 관련 믹스인\r\n@mixin autoMargin($marginX: auto) {\r\n  margin-left: $marginX;\r\n  margin-right: $marginX;\r\n}\r\n\r\n@mixin marginX($marginX: 0) {\r\n  margin-left: rem($marginX);\r\n  margin-right: rem($marginX);\r\n}\r\n\r\n@mixin marginY($marginY: 0) {\r\n  margin-top: rem($marginY);\r\n  margin-bottom: rem($marginY);\r\n}\r\n\r\n// 박스의 최소 및 최대 너비를 설정하기 위한 믹스인\r\n@mixin boxSizeMinMax($min: 768px, $max: 1200px) {\r\n  min-width: $min;\r\n  max-width: $max;\r\n  @include autoMargin;\r\n}\r\n\r\n// flex 박스 믹스인\r\n@mixin flexbox($direction: row, $justify: flex-start, $items: stretch, $wrap: nowrap) {\r\n  display: flex;\r\n  flex-flow: $direction $wrap;\r\n  justify-content: $justify;\r\n  align-items: $items;\r\n}\r\n\r\n@mixin pos($position: absolute) {\r\n  position: $position;\r\n}\r\n\r\n@mixin flexCenter($dp: flex, $jc: center, $ai: center) {\r\n  display: $dp;\r\n  justify-content: $jc;\r\n  align-items: $ai;\r\n}\r\n\r\n// 이미지 중앙 정렬 (50$)\r\n@mixin centerTranslateY($percent: -50%) {\r\n  top: 50%;\r\n  transform: translateY($percent);\r\n}\r\n\r\n@mixin centerTranslateX($percent: -50%) {\r\n  left: 50%;\r\n  transform: translateX($percent);\r\n}\r\n\r\n// iframe 비율\r\n@mixin respondIframe($w: 16, $h: 9) {\r\n  position: relative;\r\n  padding-top: math.div($h, $w) * 100%;\r\n  height: 0 !important;\r\n\r\n  iframe {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}\r\n// 버튼 스타일 제거\r\n@mixin removeBtnStyle($webkit: none, $moz: none, $appear: none, $border: none, $bg: transparent, $pd: 0) {\r\n  -webkit-appearance: $webkit;\r\n  -moz-appearance: $moz;\r\n  appearance: $appear;\r\n  border: $border;\r\n  background: $bg;\r\n  padding: $pd;\r\n}\r\n\r\n// ellipsis\r\n@mixin ellipsis($over: hidden, $to: ellipsis, $ws: nowrap) {\r\n  overflow: $over;\r\n  text-overflow: $to;\r\n  white-space: $ws;\r\n}\r\n\r\n@mixin ellipsisLine($over: hidden, $to: ellipsis, $dp: -webkit-box, $webClamp: 2, $webOrient: vertical) {\r\n  overflow: $over;\r\n  text-overflow: $to;\r\n  display: $dp;\r\n  -webkit-line-clamp: $webClamp;\r\n  -webkit-box-orient: $webOrient;\r\n}\r\n\r\n// font 설정\r\n@mixin fontSetup($weight: 400, $size: rem(12px), $color: $secondary-font, $height: 1rem, $spacing: -0.03rem) {\r\n  font-weight: $weight;\r\n  font-size: $size;\r\n  line-height: $height;\r\n  color: $color;\r\n  letter-spacing: $spacing;\r\n}\r\n\r\n// carousel button\r\n@mixin carouselButton(\r\n  $pos: absolute,\r\n  $top: 50%,\r\n  $trans: translateY(-50%),\r\n  $w: 40px,\r\n  $h: 40px,\r\n  $bg: $white,\r\n  $border: 2px solid $border-gray2,\r\n  $border-radius: 50%,\r\n  $fz: rem(26px),\r\n  $color: $font-gray1\r\n) {\r\n  position: $pos;\r\n  top: $top;\r\n  transform: $trans;\r\n  width: $w;\r\n  height: $h;\r\n  background: $bg;\r\n  border: $border;\r\n  border-radius: $border-radius;\r\n  font-size: $fz;\r\n  color: $color;\r\n}\r\n","@mixin mobile {\r\n  @media (max-width: 767px) {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin tab {\r\n  @media (max-width: 1169px) {\r\n    @content;\r\n  }\r\n}\r\n",".header-down {\r\n  @include autoMargin;\r\n\r\n  position: relative;\r\n  background: $white;\r\n  max-width: 1170px;\r\n  padding: rem(18px) rem(30px);\r\n\r\n  &__nav {\r\n    @include flexbox($items: center);\r\n\r\n    &__title {\r\n      margin-right: auto;\r\n    }\r\n  }\r\n\r\n  &__form {\r\n    position: relative;\r\n    margin-right: 8px;\r\n\r\n    &__search {\r\n      &__container {\r\n        display: none;\r\n\r\n        &.is-focus {\r\n          display: block;\r\n          position: absolute;\r\n          width: 464px;\r\n          margin-top: rem(10px);\r\n          padding-top: rem(6px);\r\n          border: 1px solid $gray0;\r\n          border-radius: 8px;\r\n          background: $white;\r\n          overflow: hidden;\r\n          opacity: 1;\r\n          transition: opacity 0.2s ease-in-out 0s;\r\n          box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.452);\r\n          z-index: 8000;\r\n        }\r\n      }\r\n\r\n      &__books {\r\n        text-align: center;\r\n        line-height: 18px;\r\n        padding-top: rem(60px);\r\n        padding-bottom: rem(60px);\r\n        color: $font-gray4;\r\n        font-size: rem(14px);\r\n        font-weight: 400;\r\n      }\r\n\r\n      &__item {\r\n        padding: rem(8px) rem(12px);\r\n      }\r\n\r\n      &__title {\r\n        display: inline-block;\r\n        margin: 0px rem(5px) rem(3px);\r\n        line-height: 22px;\r\n        font-size: rem(14px);\r\n\r\n        &__match {\r\n          color: $primary-color;\r\n        }\r\n      }\r\n\r\n      &__author {\r\n        display: inline-block;\r\n        margin: 0px rem(5px) rem(3px);\r\n        line-height: 22px;\r\n        font-size: rem(12px);\r\n        color: $gray8;\r\n      }\r\n    }\r\n\r\n    &__container {\r\n      width: 259px;\r\n      background: #e9e9e9;\r\n      border-radius: 8px;\r\n    }\r\n\r\n    &__input {\r\n      border: none;\r\n      background: #e9e9e9;\r\n      caret-color: $primary-color;\r\n\r\n      &:focus {\r\n        outline: none;\r\n      }\r\n    }\r\n\r\n    &__label {\r\n      @include flexbox($items: center);\r\n\r\n      padding: 0 rem(11px);\r\n      height: 40px;\r\n    }\r\n\r\n    &__icon {\r\n      width: 20px;\r\n      height: 20px;\r\n      color: $font-gray2;\r\n      margin-right: rem(12px);\r\n      transform: translateY(3px);\r\n\r\n      @include mobile {\r\n        transform: translateY(0px);\r\n      }\r\n    }\r\n\r\n    // Input Focus Div\r\n  }\r\n\r\n  &__list {\r\n    @include flexbox($justify: space-between);\r\n    width: 208px;\r\n  }\r\n\r\n  &__item {\r\n    @include flexCenter;\r\n    @include marginX(6px);\r\n\r\n    width: 40px;\r\n    height: 40px;\r\n    font-size: rem(26px);\r\n    color: $primary-font;\r\n\r\n    &:last-child {\r\n      margin-right: 0;\r\n    }\r\n  }\r\n\r\n  &__login {\r\n    display: none;\r\n\r\n    &__link {\r\n      display: block;\r\n      width: 100%;\r\n      padding: rem(6px) rem(12px);\r\n      border: 1px solid $border-gray1;\r\n\r\n      border-radius: 16px;\r\n    }\r\n  }\r\n}\r\n",".toaster {\r\n  position: fixed;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  top: 0;\r\n  left: 50%;\r\n  width: 400px;\r\n  padding: rem(6px) rem(6px) rem(6px) rem(26px);\r\n  color: #5abf0d;\r\n  background-color: #191919;\r\n  transform: translate3d(-50%, 0, 0);\r\n  border-radius: 5px;\r\n  animation: toast 4s ease forwards;\r\n  opacity: 0;\r\n\r\n  .toaster-text {\r\n    width: 310px;\r\n  }\r\n\r\n  .bxs-check-circle {\r\n    margin-top: rem(1px);\r\n  }\r\n\r\n  .bx-x {\r\n    color: #666666;\r\n    font-size: 30px;\r\n    margin-top: rem(1px);\r\n    cursor: pointer;\r\n  }\r\n}\r\n\r\n@keyframes toast {\r\n  0% {\r\n    opacity: 0;\r\n    transform: translate3d(-50%, 0, 0);\r\n  }\r\n\r\n  20%,\r\n  50%,\r\n  80% {\r\n    opacity: 1;\r\n    transform: translate3d(-50%, 220%, 0);\r\n  }\r\n\r\n  100% {\r\n    opacity: 0;\r\n    transform: translate3d(-50%, 0, 0);\r\n  }\r\n}","// carousel button\r\n.carousel__button {\r\n  .prev {\r\n    @include removeBtnStyle;\r\n    @include flexCenter;\r\n    @include carouselButton;\r\n\r\n    left: -20px;\r\n  }\r\n\r\n  .next {\r\n    @include removeBtnStyle;\r\n    @include flexCenter;\r\n    @include carouselButton;\r\n\r\n    right: -20px;\r\n  }\r\n}\r\n",".top-button {\r\n  @include removeBtnStyle;\r\n  @include flexCenter;\r\n\r\n  position: fixed;\r\n  bottom: 30px;\r\n  right: 25px;\r\n  width: 58px;\r\n  height: 58px;\r\n  border: 1px solid $gray7;\r\n  border-radius: 50%;\r\n  background: $white;\r\n  font-size: rem(30px);\r\n  color: $gray8;\r\n\r\n  @include mobile {\r\n    width: 40px;\r\n    height: 40px;\r\n    font-size: rem(24px);\r\n  }\r\n}\r\n","// 별점 컴포넌트\r\n\r\n.carousel {\r\n  &__star {\r\n    color: $red;\r\n    font-size: rem(14px);\r\n\r\n    @include mobile {\r\n      font-size: rem(12px);\r\n    }\r\n  }\r\n\r\n  &__rate {\r\n    color: $secondary-font;\r\n    font-size: rem(14px);\r\n\r\n    @include mobile {\r\n      font-size: rem(12px);\r\n    }\r\n  }\r\n}\r\n",".main {\r\n  &__carousel {\r\n    position: relative;\r\n    height: 400px;\r\n    margin: 0 rem(30px);\r\n    border-radius: 5px;\r\n    margin-bottom: rem(20px);\r\n\r\n    @include mobile {\r\n      margin: rem(18px);\r\n      height: 330px;\r\n    }\r\n\r\n    &__inner {\r\n      height: 100%;\r\n      overflow: hidden;\r\n    }\r\n\r\n    &__wrap {\r\n      width: 100%;\r\n      height: 100%;\r\n      display: flex;\r\n      transform: translateX(-53px);\r\n    }\r\n\r\n    &__item {\r\n      min-width: 45px;\r\n      height: 400px;\r\n      margin-left: 8px;\r\n      border-radius: 5px;\r\n      overflow: hidden;\r\n\r\n      @include mobile